@import "../00-Common/styles/sass/variables";
@import "../00-Common/styles/sass/mixins";

.mainBlock {
  min-height: 100vh;
  background-color: $bgColor;
  position: relative;;
  .bgp {
    position: fixed;
    //object-fit: cover;
    //width: 100%;
    height: 100%;
    z-index: -1;

  }
  &::before {
    position: absolute;
    content: "";
    width: 100%;
    height: 100%;
    background-color: $bgColor;
    opacity: 85%;

  }

  .mainContainer {
    @include flex(row, space-around, center, wrap);
    min-height: 100vh;

    @media screen and (max-width: 750px) {
      flex-direction: column;
    }
    img {
      height: 100%;
      width: auto;
    }
  }
}



